.widgets {
    flex: 0.3;
}

.widgets_input {
    display:flex;
    align-items: center;
    background-color: var(--twitter-background);
    padding: 10px;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 20px;
}

.widgets_input > input {
    border:none;
    background: var(--twitter-background);
}

.widgets_searchIcon {
    color: gray;
}

.widgets_widgetContainer {
    margin-top: 15px;
    margin-left: 20px;
    padding: 20px;
    background: #f5f8fa;
    border-radius: 20px;
}